<template>
  <div class="register">
    <div class="register_block">
      <h1>register</h1>
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="帳號" prop="username">
          <el-input v-model="ruleForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密碼" prop="password">
          <el-input type="text" v-model="ruleForm.password"></el-input>
        </el-form-item>
        <el-form-item label="確認密碼" prop="confirmed_password">
          <el-input type="text" v-model="ruleForm.confirmed_password"></el-input>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="email" prop="email">
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">註冊</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("請再次輸入密碼"));
      } else if (value !== this.ruleForm.password) {
        callback(new Error("兩次密碼輸入不一樣"));
      } else {
        callback();
      }
    };
    return {
      username: "",
      password: "",
      ruleForm: {
        password: "",
        confirmed_password: "",
        username: "",
        email: "",
        name: ""
      },
      rules: {
        password: [
          { required: true, message: "請輸入密碼", trigger: "blur" },
          { min: 4, max: 10, message: "密碼長度在4~10位數", trigger: "blur" }
        ],
        username: [
          { required: true, message: "請輸入帳號", trigger: "blur" },
          { min: 4, max: 10, message: "帳號長度在4~10位數", trigger: "blur" }
        ],
        confirmed_password: [{ validator: validatePass2, trigger: "blur" }],
        email: [
          { required: true, message: "請輸入email", trigger: "blur" },
          { type: "email", message: "請輸入正確的email格式", trigger: "blur" }
        ],
        name: [
          { required: true, message: "請輸入姓名", trigger: "blur" },
          { min: 2, max: 5, message: "請輸入本名非暱稱", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    ...mapActions(["register"]),
    registerUser() {
      let userdata = {
        username: this.ruleForm.username,
        password: this.ruleForm.password,
        confirmed_password: this.ruleForm.confirmed_password,
        email: this.ruleForm.email,
        name: this.ruleForm.name
      };
      console.log(userdata);
      this.register(userdata)
        .then(res => {
          console.log(res);
          if (res.data.success) {
            alert("註冊成功");
            this.$router.push("/login");
          }
        })
        .catch(err => console.log(err));
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.registerUser();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>
<style lang="scss">
.register_block {
  max-width: 600px;
  margin: 20px auto;
  text-align: center;
}
</style>