<template>
  <div class="profile">
    <h1>Profile</h1>
    <h3>{{user.username}}</h3>
    <h3>{{user.name}}</h3>
    <h3>{{user.email}}</h3>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["user"])
  },
  methods: {
    ...mapActions(["getProfile"])
  },
  created() {
    this.getProfile();
  }
};
</script>
