<template>
  <div class="login">
    <div class="login_block">
      <h1>Login</h1>
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="帳號" prop="username">
          <el-input v-model="ruleForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密碼" prop="password">
          <el-input type="password" v-model="ruleForm.password"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">登入</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
          <router-link to="register">
            <el-button>註冊</el-button>
          </router-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      ruleForm: {
        password: "",
        username: ""
      },
      rules: {
        password: [
          { required: true, message: "請輸入密碼", trigger: "blur" },
          { min: 4, max: 10, message: "密碼長度在4~10位數", trigger: "blur" }
        ],
        username: [
          { required: true, message: "請輸入帳號", trigger: "blur" },
          { min: 4, max: 10, message: "帳號長度在4~10位數", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    ...mapActions(["login"]),

    loginUser() {
      let user = {
        username: this.ruleForm.username,
        password: this.ruleForm.password
      };
      this.login(user)
        .then(res => {
          if (res.data.success) {
            alert(res.data.msg);
            this.$router.push("/profile");
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.loginUser();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>
<style lang="scss">
.login_block {
  max-width: 600px;
  margin: 20px auto;
  text-align: center;
}
</style>